.news-grid {
    padding: 0 30px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
}

.news-card {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.2s;
}

.news-card:hover {
    transform: translateY(-5px);
}

.author-wrapper {
    display: flex;
    width: 100%;
    padding: 5px 0;
}

span.author {
    margin-left: auto;
}

.news-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
}

.news-title {
    font-size: 20px;
    margin: 0 0 10px 0;
    color: #333;
}

.news-content {
    padding: 15px;
}
