header {
    max-width: 900px;
    width: 100vw;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    height: 80px;
    align-items: center;
}

header a {
    font-size: 26px;
    text-decoration: none;
    color: inherit;
}

header a:visited {
    text-decoration: none;
    color: inherit;
}

header a:hover {
    text-decoration: none;
    color: inherit;
}

.animated-link {
    position: relative;
    color: inherit;
    text-decoration: none;
    overflow: hidden;
    display: inline-block;
}

.animated-link::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: currentColor;
    bottom: 0;
    left: -100%;
    transition: left 0.3s ease-out;
}

.animated-link:hover::after {
    left: 0;
}
