.contact-card {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 100%;
    text-align: center;
}

.social-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
}

.social-links a {
    text-decoration: none;
    color: #007bff;
    font-size: 24px;
    transition: color 0.3s ease;
}

.social-links a:hover {
    color: #0056b3;
}

img {
    height: 450px;
}
